<template>
  <el-container>
    <el-header class="header">
      <div class="left">
        <div>
          <span>用户姓名：</span>
          <el-input class="com-input" placeholder="请输入用户姓名" v-model="name" />
          <span class="user-num">手机号：</span>
          <el-input class="com-input" placeholder="请输入用户手机号" v-model="mobile" />
          <span class="user-num">保单号：</span>
          <el-input class="com-input" placeholder="请输入用户保单号" v-model="policyNum" />
        </div>
        <div class="area">
          <span>证件号码：</span>
          <el-input class="com-input" placeholder="请输入用户证件号码" v-model="comName" />
          <span>所属企业：</span>
          <el-select v-model="company" class="com-input" placeholder="全部">
            <el-option v-for="item in companys" :key="item.value" :label="item.label" :value="item.value" />
          </el-select>
        </div>
        <div class="area">
          <span class="text">地区：</span>
          <el-select class="com-input" v-model="proValue" placeholder="全部">
            <el-option v-for="item in province" :key="item.value" :label="item.label" :value="item.value" />
          </el-select>
          <el-select class="com-input" v-model="cityValue" placeholder="全部">
            <el-option v-for="item in city" :key="item.value" :label="item.label" :value="item.value" />
          </el-select>
          <el-select class="com-input" v-model="areaValue" placeholder="全部">
            <el-option v-for="item in area" :key="item.value" :label="item.label" :value="item.value" />
          </el-select>
        </div>
      </div>
      <div class="right">
        <el-button type="primary" icon="el-icon-search" class="btn">查询</el-button>
        <el-button type="primary" @click="addCom" class="btn">新增用户</el-button>
      </div>
    </el-header>
  </el-container>
</template>
<script>
import { areaProvince } from "../../../../data/hosNetwork";
import { companys } from "../../../../data/userManage";
import _ from "lodash";
export default {
  name: "listOptions",
  data() {
    return {
      companys,
      areaProvince,
      company:'',
      name:'',
      cardNum:'',
      mobile:'',
      policyNum:'',
      province: [],
      city: [],
      area: [],
      gradeValue: "",
      proValue: "",
      cityValue: "",
      areaValue: ""
    };
  },
  mounted: function() {
    this.handleProvince();
  },
  methods: {
    addCom() {
      this.$router.push("addUser?type=add");
    },
    handleProvince() {
      //处理省份
      let province = _.map(areaProvince, (item, key) => {
        let obj = {};
        obj.value = item.value;
        obj.label = item.label;
        return obj;
      });
      this.province = province;
    }
  },
  watch: {
      proValue: function(curVal, oldVal){
          if( !curVal ) return;
          let citys = _.filter(areaProvince, (item, key) => {
            return item.value === curVal;
          });
          let city = _.map(citys[0].children, (item, key) => {
            let obj = {};
            obj.value = item.value;
            obj.label = item.label;
            return obj;
          });
          this.cityValue = '';
          this.areaValue = '';
          this.city = city;
      },
      cityValue: function(curVal, oldVal){
          if( !curVal ) return;
          let citys = _.filter(areaProvince, (item, key) => {
            return item.value === this.proValue;
          });
          let areas = _.filter(citys[0].children, (item, key) => {
            return item.value === curVal;
          });
          let area = _.map(areas[0].children, (item, key) => {
            let obj = {};
            obj.value = item.value;
            obj.label = item.label;
            return obj;
          });
          this.areaValue = '';
          this.area = area;
      }
  }
};
</script>
<style lang="less" scoped>
@import url("./../../../../assets/less/color.less");
.header {
  display: flex;
  text-align: left;
  border-bottom: 1px solid @border-gray;
  height: inherit !important;
  padding-bottom: 20px;
  .user-num{
    display: inline-block;
    text-indent: 1em;
  }
  .area {
    margin-top: 20px;
    .text {
      display: inline-block;
      text-indent: 2em;
    }
    
  }
  .com-input {
    width: 210px;
    margin-right: 20px;
  }
  .right {
    display: flex;
    align-items: center;
    .btn {
      margin-left: 20px;
    }
  }
}
</style>